// DoashBoard 面板
import request from '@/apis/request.wms'

/*
*   消息管理
*/
// 1. 消息列表列表
export function getMessageListApi(data) {
  return request({
    url: '/wms/task/msgRemind/pageList',
    method: 'post',
    data
  })
}

// 2. 消息已读
export function handleMessageStatusApi(data) {
  return request({
    url: '/wms/task/msgRemind/readMsg',
    method: 'post',
    data
  })
}
