<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="isRead">
          <el-select v-model="query.isRead" placeholder="消息状态搜索" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in readTypeOpts"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="type">
          <el-select v-model="query.type" filterable clearable placeholder="消息类型" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in dict['wms_msg_remind_type']"
              :key="item.id"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-button class="filter-item" type="success" icon="el-icon-search" @click="init">搜索</el-button>
        <el-button class="filter-item" type="warning" icon="el-icon-refresh-left" @click="resetForm('filterItem')">重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :refresh-key="refreshKey"
        :loading="loading"
        :index="true"
        :index-fixed="false"
        :selection="false"
        :multiple="false"
        :selection-fixed="false"
        :table-column-sort="false"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="showOperate"
        :operate-fixed="false"
        :table-selection="tableSelection"
        :operation-width="100"
        @selectionChange="selectionChange"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <!-- 消息未读状态 -->
          <el-link v-if="!scope.row.isRead" type="danger" @click="handleMarkRead(scope.row)">未读</el-link>
          <!-- 消息已读状态 -->
          <template v-else>
            <el-link v-if="!scope.row.link" type="info">已读</el-link>
            <el-link v-else type="info" @click="hanleViewLink(scope.row)">查看</el-link>
          </template>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import Pagination from '@/components/Pagination'
import { getMessageListApi, handleMessageStatusApi } from '@/apis/modules/common/message'

export default {
  name: 'MesDashboardMessage',
  components: {
    Pagination
  },
  mixins: [paginationPage],
  dicts: ['wms_msg_remind_type'],
  data() {
    return {
      query: {
        type: '',
        isRead: ''
      },
      readTypeOpts: [
        {
          value: 0,
          label: '未读'
        },
        {
          value: 1,
          label: '已读'
        }
      ],
      refreshKey: 1, // 表格唯一key
      showOperate: true, // 是否展示表格操作栏
      loading: false, // 表格loading
      tableData: [], // 表格数据
      tableSelection: [], // 表格选中数据
      columnList: [
        {
          module: 'word',
          prop: 'title',
          label: '标题',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'content',
          label: '内容',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'dataSources',
          label: '来源',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: c => {
            const map = { 0: 'WMS', 1: 'SAP' }
            return map[c.dataSources]
          },
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'fromUserName',
          label: '发送人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'remidTime',
          label: '发送日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'toUserName',
          label: '接收人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  computed: {},
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getMessageListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(item => {
              const { type, ...info } = item
              let type_show = ''
              if (type === 0) {
                type_show = '回款计划提醒'
              } else if (type === 1) {
                type_show = '拜访提醒'
              }
              return {
                type_show,
                ...info
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip 标记已读
    handleMarkRead(item) {
      handleMessageStatusApi({
        id: item.id
      }).then(res => {
        const { code } = res
        if (!code) {
          this.$notify({
            title: `${item.title} 标记已读！`,
            type: 'success'
          })
          this.init()
          this.hanleViewLink(item)
        }
      })
    },
    // Tip 查看
    hanleViewLink(item) {
      if (!item.link) return
      // item.type 1：检验；2,3,4：库存台账
      // serviceId：业务ID
      // paramMap：参数map
      const params = item.param ? item.paramMap || { serviceId: item.serviceId } : { serviceId: item.serviceId }
      // Tip: "pageSource"字段,标识跳转页面来源。用来区分特定场景(当从当前<系统消息管理>页面跳转到<设备刻度记录>页面需要打开新增弹窗)
      this.$router.push({
        name: item.link,
        params: Object.assign({}, params, { pageSource: 'message' }),
        query: item.query ? item.queryMap : {}
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    }
  }
}
</script>

<style lang='scss' scoped>
</style>
